import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";
import api from "@/microApps/eventCenter/api";

@Component({})
export default class EventTemplateList extends Vue {
  loading: boolean = false;
  oparation: any = "add"; //保存操作，add：添加，edit：编辑
  selectIdx: any = 0;
  typeList: Array<any> = []; //基础属性类型列表
  //当前选中的基础属性类型
  baseTypeSelect: any = {
    id: "",
    baseTypeName: "",
    sort: ""
  };
  baseTypeEdit: any = {};
  tableList: Array<any> = []; //基础属性表格数据
  searchDTO: any = {
    current: 1,
    size: 10,
    total: 0,
    query: {
      baseTypeId: "",
      typeCode: ""
    }
  }; //表格查询参数

  mounted() {
    this.getAttrsList();
  }
  // 切换到车辆详情页面（添加和编辑）
  switchToTemplateDetails() {}
  /**
   * 自定义属性相关
   */
  //获取基础属性类型对应的自定义属性列表
  getAttrsList() {
    this.loading = true;
    this.$http
      .post(api.getEventTemplate, this.searchDTO)
      .then((res: any) => {
        if (res.data.status) {
          const result = res.data.data;
          this.tableList = result.records;
          this.searchDTO.current = result.current;
          this.searchDTO.total = result.total;
        } else {
          this.$message.error(res.data.message);
        }
      })
      .finally(() => {
        this.loading = false;
      });
  }

  handleAddTemplate() {
    this.$router.push("/eventTemplateEdit");
  }

  handleEditTemplate(item: any) {
    this.$router.push({
      path: "/eventTemplateEdit",
      query: {
        id: item.id
      }
    });
  }

  //删除自定义属性
  deleteAttr(attrId: string) {
    let _this = this;
    this.$confirm("确认删除?", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    }).then(() => {
      this.$http.get(api.deleteEventTemplate + "/" + attrId).then((res: any) => {
        if (res.status) {
          if (res.data.status) {
            this.$message.success("删除成功!");
            this.getAttrsList();
          } else {
            _this.$message.error(res.data.message);
          }
        }
      });
    });
  }
}
